import React, { useEffect, useState } from 'react';
import { getBranches, getCommits } from './api';
import Tree from 'react-d3-tree';

const CommitGraph = ({ username, repo, token }) => {
  const [data, setData] = useState([]);

  useEffect(() => {
    if (username && repo && token) {
      getBranches(username, repo, token)
        .then((response) => {
          const branches = response.data;
          const commitData = {}; // This will hold all commit data

          const allBranchPromises = branches.map(branch =>
            getCommits(username, repo, branch.name, token)
              .then((response) => {
                response.data.forEach(commit => {
                  const commitSHA = commit.sha.substring(0, 7);
                  if (!commitData[commitSHA]) {
                    commitData[commitSHA] = { name: commitSHA, children: [] };
                  }

                  commit.parents.forEach(parent => {
                    const parentSHA = parent.sha.substring(0, 7);
                    if (!commitData[parentSHA]) {
                      commitData[parentSHA] = { name: parentSHA, children: [] };
                    }
                    commitData[parentSHA].children.push(commitData[commitSHA]);
                  });
                });
              }));

          // Wait for all branch promises to resolve
          Promise.all(allBranchPromises).then(() => {
            const roots = Object.values(commitData).filter(node => node.children.length === 0);
            setData(roots);
          });
        });
    }
  }, [username, repo, token]);

  return (
    <div style={{ width: '100%', height: '800px' }}>
      {data.length > 0 && (
        <Tree data={data} orientation="vertical" />
      )}
    </div>
  );
};

export default CommitGraph;
